* {
  box-sizing: border-box;
}
.App {
  font-family: "Raleway";
  background-color: #e2bc6a;
  max-width: 100%;
  padding-bottom: 30px;
}
.heading-font {
  font-family: "Cinzel Decorative";
  font-size: 4em;
  margin: 0;
  padding-top: 10px;
  color: rgb(6, 44, 63);
  text-align: center;
}
.tagline {
  color: rgb(4, 4, 4);
  text-align: center;
}
.form-style {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 30px;
  margin-bottom: 40px;
}
.ip-style {
  width: 40%;
  font-size: 2vw;
  border: 2px solid rgb(6, 44, 63);
  outline: none;
  padding-left: 20px;
  border-right: none;
  border-top-left-radius: 40px;
  border-bottom-left-radius: 40px;
  background-color: lightgray;
}
.btn-style {
  width: 15%;
  font-size: 2.6vw;
  border: 2px solid rgb(6, 44, 63);
  border-left: 1px solid gray;
  /* border-left: none; */
  outline: none;
  cursor: pointer;
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  background-color: lightgray;
}

